import {FollowCamera} from "@babylonjs/core/Cameras/followCamera";
import {Vector3} from "@babylonjs/core/Maths/math";
import {Scene} from "@babylonjs/core/scene";
import {AbstractMesh} from "@babylonjs/core";

export class GameCamera extends FollowCamera {
    
    constructor(scene: Scene, lockedTarget: AbstractMesh) {
        super("GameCamera", new Vector3(0, 10, -10), scene, lockedTarget);
        this.reset();
        this.cameraAcceleration = 0.05;
        this.maxCameraSpeed = 400;
        this.attachControl(true);
    }
    
    reset() {
        this.radius = 10;
        this.heightOffset = 4;
        this.rotationOffset = 0;
    }
}